.nav-header {
    position: relative;
    position: fixed;
    top: 0;
    right: 0;
    left: 0;
    z-index: 1000;
    width: 100%;
    height: 80px;
    background: rgba(#ffffff,.95);
    &__logo {
        position: absolute;
        top: 20px;
        left: 30px;
        font-size: 20px;
        font-weight: bold;
        vertical-align: center;
        padding-left: 10px;
        color: $color_black;
    }
    &__menu {
        list-style: none;
        text-align: right;
        padding-top: 20px;
    }
    &__menu-item {
        font-size: 13px;
        font-weight: bold;
        display: inline;
        padding: 0px 20px;
    }
    &__link {
        color: $color_black;
        &:hover {
            &:after {
                opacity: 1;
                width: 100%;
            }
        }
    }
}